#searchnode {
  visibility: hidden;
  height: 0;
  padding: 0;
  margin: 0;
}

.searchBox {
  position: absolute;
  background-color: rgb(49, 51, 49);
  height: 40px;
  border-radius: 40px;
  padding: 10px;

  &:hover > .searchInput {
    width: 240px;
    max-width: 240px;
    padding: 0 6px;
  }

  &:hover > .searchButton {
    background: white;
    background-color: rgb(49, 51, 49);
  }

  &:hover > .closeButton {
    background: transparent;
  }
}

.MuiSvgIcon-colorAction {
  color: white;
}

.searchButton {
  color: white;
  float: right;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgb(49, 51, 49);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
}

.closeButton {
  background: transparent;
  margin-right: 5px;
  border: none;
}

.searchInput {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: white;
  font-size: 16px;
  transition: 0.4s;
  line-height: 40px;
  width: 0px;
}

@media screen and (max-width: 620px) {
  .searchBox:hover > .searchInput {
    width: 150px;
    padding: 0 6px;
  }
}

.nodes {
  fill: darkgray;
}
.nodes text {
  fill: white;
}

