.snackbar {
  background: var(--gradient-lr-blue, white);
  display: flex;
  justify-content: center;
}

.tooltip {
  padding: 0;
  margin: 0;

  &__container {
  }

  &__header {
    padding: 5px !important;
    margin: 0 !important;

    & > h3 {
      text-align: center;
    }
  }

  &__content {
    padding: 10px !important;
    margin: 0 !important;
  }
}


@keyframes pulseMe 
{       
  0% {-webkit-transform: scale(1.0); transform: scale(1.0); opacity: 1;}

  50% {-webkit-transform: scale(1.1); transform: scale(1.1); opacity: 1;}

  100% {-webkit-transform: scale(1.0); transform: scale(1.0); opacity: 1;}
}

@keyframes border-pulsate {
  0%   { border-color: rgba(0, 0, 0, 1); }
  50% { border-color: rgba(0, 0, 0, 0.5); }
  100%   { border-color: rgba(0, 0, 0, 1); }
}

.pulseMe
{
  /* Giving Animation Function */
  -webkit-animation: pulseMe 1s ease infinite, border-pulsate 1s ease infinite;
  animation: pulseMe 1s ease infinite , border-pulsate 1s ease infinite;
}

.blur {
  -webkit-filter: blur(10px);
  filter: blur(10px);
}

.shadow {
-webkit-filter: drop-shadow( 0px 0px 5px white );
        filter: drop-shadow( 0px 0px 5px #000 ); /* Same syntax as box-shadow */
}

